  margin: 0;
}
h1,h2,h3,h4,h5,h6{
  margin: 0;
}
ul,ol,li{
  margin: 0;
  padding: 0;
  list-style: none;
}
a{
  color: black;
  text-decoration: none;
  cursor: pointer;
}
input,button,textarea{
  margin: 0;
  padding: 0;
  outline: none;
  height: 30px;
  border: 1px solid #cccccc;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  vertical-align: top;
}
button{
  cursor: pointer;
}
button::-moz-focus-inner {
  border:0;
  padding:0;
}
textarea{
  resize: none;
}
img{
  border: none;
  vertical-align: top;
}
table{
  border-collapse: collapse;
}
tr,th,td{
  padding: 0;
}
p{
  margin: 0;
}
strong{
  font-weight: normal;
}
em,i,cite,var,address,dfn{
  font-style: normal;
}
body{
  -webkit-text-size-adjust: 100%;
}
input,button,a{
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}
input,button{
  -webkit-appearance: none;
  border-radius: 0;
